h1 {
    margin: 30px 20px 10px 20px !important;
    font-size: 2em;
    color: #2c3e50;
  }
  
  .defaultButton {
    color: #fff;
    background-color: #2c3e50;
    border-color: #2c3e50;
  }
  
  /************************** NAVIGATION **************************/
  
  .navbar {
    background-color: #1abc9c;
    padding: 0px;
    height: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
  
  .create-circuit {
    color: #007E65;
    font-weight: bold;
    margin: 0px;
  }
  
  .ml-auto {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    font-size: .7em;
  }
  
  .ml-auto > li {
    margin-left: 50px;
  }
  
  .flex-end {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    width: 100%;
  }
  
  .logo {
    width: 135px;
    margin-left: 20px;
  }
  
  .auth-text {
    display: block;
    margin: auto 30px auto auto;
    color: #004C3D;
    cursor: pointer;
    font-size: .7em;
    font-weight: bold;
    background-color: white;
    border-radius: 2px;
    width: 140px;
    text-align: center;
    transition: .1s;
  }
  
  .user-icon {
    margin-right: 20px;
    margin-left: 20px;
    color: white;
  }
  
  .ant-dropdown-link {
    color: white !important;
  }
  
  /************************** AUTHENTIFICATION **************************/
  
  .modal-header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
  }
  
  .modal-header:hover {
    cursor: pointer;
  }
  
  .active > * {
    color: #1abc9c;
  }
  
  /************************** HOME **************************/
  
  .menu-wrapper {
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: calc(10px + 2vmin);
    color: #2c3e50;
  }
  
  .toggle-button {
    background-color: #1abc9c;
    padding: 8px;
    position: fixed;
    bottom: 30px;
    right: 30px;
    border-radius: 4px;
    width: 120px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
  }
  
  .toggle-button > p {
    margin: 0px;
    padding-right: 10px;
    text-align: center;
    color: white;
    font-weight: bold;
  }
  
  /************************** CIRCUIT-VIEW **************************/
  
  .my-circuits-header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
  
  .button-dropdown {
    height: 40px;
    display: block;
    margin: 30px 20px 10px 10px;
  }
  
  .header-buttons {
    display: -webkit-flex;
    display: flex;
  }
  
  .item-wrapper {
    padding: 0px;
    list-style-type: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }
  
  
  .list-item {
    background-color: #ecf0f1;
    border-radius: 5px;
    width: 30%;
    height: 200px;
    margin: 20px;
    padding: 15px;
    transition: .2s;
    position: relative;
  }
  
  .list-item > h3 {
    transition: .2s;
  }
  
  .list-item:hover {
    cursor: pointer;
    background-color: #65D8C1;
    color: #fff;
    transition: .2s;
  }
  
  .list-item:hover > h3, .list-item:hover > .bold-info-circuit {
    color: #fff;
    transition: .2s;
  }
  
  .version-item  {
    position: absolute;
    background-color: white;
    color: #2c3e50;
    border-radius: 2px;
    padding: 0px 5px;
    width: 90px;
    text-align: center;
    bottom: 0px;
    right: 10px;
  }
  .published-item {
    position: absolute;
    top: 15px;
    right: 40px;
  }
  
  .level-item {
    position: absolute;
    top: 15px;
    right: 10px;
    width: 15px;
    height: 15px;
    border-radius: 100%;
  }
  
  .easy {
    background-color:#27ae60; 
  }
  
  .medium {
    background-color:#2980b9; 
  }
  
  .hard {
    background-color:#c0392b;
  }
  
  .html-reformat {
    max-height: 50px;
    overflow: hidden;
  }
  
  /************************** CIRCUIT-PUBLISHER **************************/
  .view-wrapper {
    display: -webkit-flex;
    display: flex;
    height: 100%;
  }
  
  .new-map {
    width: 80%;
  }
  
  .scroll-menu {
    height: 100%;
    width: 20%;
    background-color: #ecf0f1;
  }
  
  .circuit-title {
    color: #2c3e50;
    width: 100%;
    text-align: center;
    padding: 10px;
    margin: 0px;
    height: 15%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
  }
  
  .circuit-title > h3 {
    font-size: 1.3em;
  }
  
  .circuit-title > button {
    width: 85px;
    display: block;
    margin: 0px auto;
  }
  
  .step-wrapper {
    height: 85%;
    overflow: scroll;
    padding: 7px;
    margin: auto;
    display: block;
  }
  
  .step-wrapper-item {
    cursor: pointer;
    display: -webkit-flex;
    display: flex;
    padding: 10px;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
    height : 70px;
    list-style: none;
    position: relative;
    transition: .2s;
    margin-bottom: 7px;
    background-color: white;
  }
  
  .step-wrapper-item:nth-last-child(1) {
    margin-bottom: 0;
  }
  
  .step-wrapper-item:hover{
    color: white;
    background-color: #1abc9c;
    transition: .2s;
  }
  
  .step-name {
    font-weight: bold;
  }
  
  .order-item {
    position: absolute;
    top: 5px;
    right: 25px;
  }
  
  .update-circuit-button {
    padding: auto;
    line-height: 10px;
    height: 25px;
    margin: auto 10px;
    display: block;
  }
  
  .update-circuit-button:hover {
  cursor: pointer;
  }
  
  .update-circuit {
    display: block;
    background-color: white;
  }
  
  .hidden-update-circuit {
    display: none;
  }
  
  .update-step {
    display: block;
    background-color: white;
  }
  
  .hidden-update-step {
    display: none;
  }
  
  .update-circuit, 
  .hidden-update-circuit,
  .update-step,
  .hidden-update-step {
    position: absolute;
    top: 50px;
    left: 50%;
    bottom: 0px;
    width: 30%;
    z-index: 2;
  }
  
  .update-title {
    height: 15%;
    display: -webkit-flex;
    display: flex;
  }
  
  .update-title > h3 {
    font-size: 1.3em;
    width: 80%;
    text-align: center;
    margin: auto;
  }
  
  .update-form {
    height: 85%;
    overflow: scroll;
  }
  
  .form-group {
    padding: 10px 0px;
    max-width: 80%;
    display: block;
    margin: auto;
  }
  
  .form-group > label {
    font-weight: bold;
    color: #1abc9c;
  }
  
  .form-group > textarea {
    min-height: 100px;
  }
  
  .update-buttons {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    margin: 20px;
  }
  
  .update-buttons > button{
    margin: 0px 10px;
  }
  
  .focus-step {
    background-color: #007E65;
    color: white;
  }
  
  .come-back-button {
    background-color: #1abc9c;
    color: white;
    font-weight: bold;
    padding: 8px;
    position: fixed;
    bottom: 30px;
    left: 30px;
    border-radius: 4px;
    width: 100px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    cursor: pointer;
  }
  
  .rich-text-editor {
    background-color: white;
  }
  
  .nav-tabs {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin-top: 30px;
  }
  
  .nav-item {
    width: 33%;
    text-align: center;
    font-size: .9em;
  }
  
  .nav-item:hover {
    cursor: pointer;
  }
  
  .active {
    font-weight: bold;
  }
  
  .dropdown-question-score {
      display: block !important;
      margin: 20px auto !important;
  }
  
  .ant-checkbox-wrapper {
    display: -webkit-flex !important;
    display: flex !important;
    -webkit-justify-content: center;
            justify-content: center;
    margin : 20px auto !important;
    width: 210px !important;
  }
  
  .bold-info-circuit {
      font-weight: bold;
      color: #004C3D;
      margin: 20px 0px 20px 0px;
      transition: .2s;
  }
  
  /************************** DETAILED-CIRCUIT **************************/
  
  .header-wrapper {
      display: -webkit-flex;
      display: flex;
      -webkit-justify-content: space-between;
              justify-content: space-between;
  }
  
  .name-score {
    color: #2c3e50;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    height: 20%;
  }
  
  .rating-wrapper {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: row;
              flex-direction: row;
      -webkit-justify-content: flex-start;
              justify-content: flex-start;
  }
  
  .rating-wrapper > p {
      display: block;
      margin: 0px 20px 10px 0px;
  }
  
  .ant-rate {
      margin: 0px 0px 10px 20px !important;
  }
  
  .favoris-button {
      margin: 30px 20px 20px 20px;
  }
  
  .bottom-wrapper {
    color: #2c3e50;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 80%;
  }
  
  .circuit-infos {
    background-color: #ecf0f1;
    border-radius: 3px;
    padding: 15px;
    margin: 20px;
  }
  
  .circuit-comments {
    border-radius: 3px;
    padding: 15px;
    margin: 20px;
  }
  
  .comments-title {
    font-size: 1.5em;
    color: #1abc9c;
  }
  
    /************************** ACCOUNT **************************/
  
    .account-header-wrapper {
        height: 200px;
        display: -webkit-flex;
        display: flex;
    }
  
    .account-header-wrapper > h2 {
        margin: 0px;
    }
  
    .avatar-picture {
        border-radius: 100px;
        width: 170px;
        height: 170px;
        background-color: #981CEB;
        margin: auto;
        border: 5px solid #15202b;
    }
  
    .avatar-wrapper {
      display: -webkit-flex;
      display: flex;
      margin: 20px;
      width: 25%;
    }
  
    .edit-profil {
        display: block;
        margin: auto 30px;
    }
  
    .user-activity {
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: center;
                justify-content: center;
        width: 75%;
        height: 70px;
        margin-top: 30px;
    }
  
    .user-activity > div {
        font-size: 1.2em;
        margin: 10px 50px;
        cursor: pointer;
        transition: .2s;
    }
  
    .user-activity > div:hover {
      transition: .2s;
      color: #1abc9c;
  }
  
    .user-activity > div >p {
        text-align: center;
        margin: 0px;
    }
  
    .account-body-wrapper {
        display: -webkit-flex;
        display: flex;
    }
  
    .basic-informations-wrapper {
        background-color: #ecf0f1;
        margin: 20px;
        border-radius: 3px;
        width: 25%;
        height: 220px;
        padding: 30px;
    }
  
    .feed-wrapper {
        top: -115px;
        position: relative;
        background-color: #ecf0f1;
        margin: 20px;
        border-radius: 3px;
        width: 75%;
        padding: 30px;
        min-height: 338px;
    }

    .feed-wrapper > ul {
        margin-top: 50px;
    }
  
    .account-active-tab > p {
      color: #1abc9c;
    }

    .little-avatar-picture {
        border-radius: 20px;
        width: 40px;
        height: 40px;
        background-color: #981CEB;
    }

    .relation-item {
        display: -webkit-flex;
        display: flex;
        margin: 15px 0px;
    }

    .relation-item > p {
        width: 30%;
    }

    .user-relation-item {
      margin: auto 10px;
        display: block;
    }

